<template>
  <div class="container mt-4">
    <h1>Catalogues</h1>
    <div v-if="loading" class="spinner-border" role="status">
      <span class="visually-hidden">Loading...</span>
    </div>
    <div v-if="error" class="alert alert-danger">{{ error }}</div>
    <div v-if="catalogs.length" class="list-group">
      <router-link
        v-for="catalog in catalogs"
        :key="catalog.id"
        :to="{ name: 'CatalogDetail', params: { id: catalog.documentId } }"
        class="list-group-item list-group-item-action"
      >
        {{ catalog.title }}
      </router-link>
    </div>
    <div v-else-if="!loading">
      <p>No catalogs found.</p>
    </div>
  </div>
</template>

<script>
import strapiApi from '@/services/strapiApi';

export default {
  name: 'CatalogList',
  data() {
    return {
      catalogs: [],
      loading: true,
      error: null,
    };
  },
  async created() {
    try {
      const response = await strapiApi.getCatalogs();
      this.catalogs = response.data.data;
    } catch (err) {
      this.error = 'Failed to fetch catalogs.';
      console.error(err);
    } finally {
      this.loading = false;
    }
  },
};
</script>